import React, { Component } from "react";
import { withRouter } from "react-router-dom";

// Externals
import PropTypes from "prop-types";
import compose from "recompose/compose";
import validate from "validate.js";
import _ from "underscore";

// Material helpers
import { withStyles, IconButton } from "@material-ui/core";

import InputAdornment from "@material-ui/core/InputAdornment";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import { connect } from "react-redux";

import { LoginUserApp } from "../../actions/user";
// Material components
import {
  Grid,
  Button,
  CircularProgress,
  TextField,
  Typography
} from "@material-ui/core";

// Component styles
import styles from "./styles";

// Form validation schema
import schema from "./schema";

const images = {
  logo: `${process.env.PUBLIC_URL}/common/logo.png`
};
class SignIn extends Component {
  state = {
    values: {
      email: "",
      password: ""
    },
    touched: {
      email: false,
      password: false
    },
    errors: {
      email: null,
      password: null
    },
    isValid: false,
    isLoading: false,
    submitError: null,
    showPassword: false
  };

  validateForm = _.debounce(() => {
    const { values } = this.state;

    const newState = { ...this.state };
    const errors = validate(values, schema);

    newState.errors = errors || {};
    newState.isValid = errors ? false : true;

    this.setState(newState);
  }, 300);

  handleFieldChange = (field, value) => {
    const newState = { ...this.state };

    newState.submitError = null;
    newState.touched[field] = true;
    newState.values[field] = value;

    this.setState(newState, this.validateForm);
  };

  handleSignIn = async () => {
    try {
      const { history, loginApp } = this.props;
      const { values } = this.state;
      const { email, password } = values;
      this.setState({ isLoading: true });
      const response = true; //await loginApp({ userName: email, password: password });
      if (email === "admin@vuelatours.mx" && password === "admin2020") {
        if (response) {
          history.push("/admin");
        } else {
          this.setState({ isLoading: false, submitError: "Datos incorrectos" });
        }
      } else {
        this.setState({ isLoading: false, submitError: "Datos incorrectos" });
      }
    } catch (error) {
      this.setState({
        isLoading: false,
        submitError: error
      });
    }
  };

  handleClickShowPassword = () => {
    const { showPassword } = this.state;
    this.setState({ showPassword: !showPassword });
  };

  handleMouseDownPassword = event => {
    event.preventDefault();
  };
  render() {
    const { classes } = this.props;
    const {
      values,
      touched,
      errors,
      isValid,
      submitError,
      showPassword,
      isLoading
    } = this.state;

    const showEmailError = touched.email && errors.email;
    const showPasswordError = touched.password && errors.password;

    return (
      <div className={classes.root}>
        <Grid className={classes.grid} container>
          <Grid className={classes.quoteWrapper} item lg={5}>
            <div className={classes.quote}>
              <div className={classes.quoteInner}>
                <img className={classes.imgapp} src={images.logo} alt="#" />
              </div>
            </div>
          </Grid>
          <Grid className={classes.content} item lg={7} xs={12}>
            <div className={classes.content}>
              <div className={classes.contentBody}>
                <form className={classes.form} autoComplete="off">
                  <Typography className={classes.title} variant="h2">
                    Inicia Sesión
                  </Typography>
                  <Typography className={classes.subtitle} variant="body1">
                    Usar una cuenta local para entrar
                  </Typography>

                  <div className={classes.fields}>
                    <TextField
                      className={classes.textField}
                      label="Correo"
                      name="email"
                      onChange={event =>
                        this.handleFieldChange("email", event.target.value)
                      }
                      type="text"
                      autoComplete="off"
                      value={values.email}
                      variant="outlined"
                    />
                    {showEmailError && (
                      <Typography
                        className={classes.fieldError}
                        variant="body2"
                      >
                        {errors.email[0]}
                      </Typography>
                    )}
                    <TextField
                      autoComplete="off"
                      className={classes.textField}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton
                              edge="end"
                              aria-label="alternar la visibilidad de la contraseña"
                              onClick={this.handleClickShowPassword}
                              onMouseDown={this.handleMouseDownPassword}
                            >
                              {showPassword ? (
                                <VisibilityOff />
                              ) : (
                                <Visibility />
                              )}
                            </IconButton>
                          </InputAdornment>
                        )
                      }}
                      label="Contraseña"
                      name="password"
                      onChange={event =>
                        this.handleFieldChange("password", event.target.value)
                      }
                      type={showPassword ? "text" : "password"}
                      value={values.password}
                      variant="outlined"
                    />
                    {showPasswordError && (
                      <Typography
                        className={classes.fieldError}
                        variant="body2"
                      >
                        {errors.password[0]}
                      </Typography>
                    )}
                  </div>
                  {submitError && (
                    <Typography className={classes.submitError} variant="body2">
                      {submitError}
                    </Typography>
                  )}
                  {isLoading ? (
                    <CircularProgress className={classes.progress} />
                  ) : (
                    <Button
                      className={classes.signInButton}
                      color="primary"
                      disabled={!isValid}
                      onClick={this.handleSignIn}
                      size="large"
                      variant="contained"
                    >
                      Entrar
                    </Button>
                  )}
                </form>
              </div>
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}

SignIn.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    idusers: state.user.idusers
  };
};

const mapDispatchToProps = dispatch => ({
  loginApp: data => dispatch(LoginUserApp(data))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(compose(withRouter, withStyles(styles))(SignIn));
