import React, { Component } from "react";
import PropTypes from "prop-types";
import { Grid } from "@material-ui/core";
import compose from "recompose/compose";
import { withRouter } from "react-router-dom";

import { Dashboard as DashboardLayout } from "../../layouts";
import { withStyles, Typography } from "@material-ui/core";
import {
  UsersForm,
  UsersTable,
  FormSteps,
  TableGrid,
  ContainsGrid
} from "./components";

//redux
import { connect } from "react-redux";
import {
  AddCircuito,
  AddSteps,
  AddPoints,
  getPoints,
  getSteps,
  getCircuitos,
  downloadPDF,
  getContainsTours,
  postAddContainsTour
} from "../../actions/user";

const styles = theme => ({
  root: {},
  item: {
    height: "100%"
  },
  bodyHeader: {
    background:
      "radial-gradient(at 50% -20%, #1967a9, #0a1832) fixed !important"
  },
  textTitle: {
    color: "#ffffff",
    paddingTop: 15,
    paddingBottom: 15,
    marginLeft: 10
    //marginTop: 15
  }
});

const dataTable = [
  {
    id: 1,
    name: "MÉXICO"
  },
  {
    id: 2,
    name: "PARÍS"
  }
];

class Cotizador extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSteps: false,
      id: null,
      editSteps: {},
      isAdd: false,
      editCircuito: {}
    };
  }

  componentDidMount = async () => {
    const { get_Circuitos } = this.props;

    let response = await get_Circuitos();
  };

  handleIsViewFormSteps = (band, editSteps = null) => {
    this.setState({ isSteps: band, editSteps: editSteps });
  };

  handleAdd = async (band, editCircuito = null) => {
    const { get_ContainsTours } = this.props;
    this.setState({
      isAdd: band,
      editCircuito,
      id: editCircuito ? editCircuito.id : null
    });
    if (band && editCircuito) {
      await this.handleRefreshSteps(editCircuito.id);
      await get_ContainsTours(editCircuito.id);
    }
  };
  handleOnSubmit = async values => {
    const { Add_Circuito } = this.props;
    let response = await Add_Circuito(values);
    if (response) {
      this.setState({ id: response.id });
      this.handleRefreshSteps(response.id);
      return true;
    }
  };

  handleRefresh = async id => {
    const { get_Points } = this.props;
    let response = await get_Points(id);
    return response;
  };

  handleRefreshSteps = async id => {
    const { get_Steps } = this.props;
    let response = await get_Steps(id);
    return response;
  };

  handleAddConatinsTour = async data => {
    const { post_AddContainsTour, get_ContainsTours } = this.props;
    const { id } = this.state;
    let response = await post_AddContainsTour(data);
    await get_ContainsTours(id);

    return true;
  };

  render() {
    const {
      classes,
      Add_Steps,
      Add_Points,
      points,
      steps,
      circuitos,
      download_PDF,
      cointainsTour
    } = this.props;
    const { isSteps, id, editSteps, isAdd, editCircuito } = this.state;
    return (
      <DashboardLayout title="Administración">
        <div className={classes.root}>
          <div className={classes.bodyHeader}>
            <Typography variant={"h2"} className={classes.textTitle}>
              COTIZADOR
            </Typography>
            <Grid container spacing={2}>
              {!isAdd ? (
                <Grid item lg={12} sm={12} xl={12} xs={12}>
                  <TableGrid
                    data={circuitos}
                    onAdd={this.handleAdd}
                    downloadEvent={download_PDF}
                  />
                </Grid>
              ) : null}

              <Grid item lg={8} sm={6} xl={3} xs={12}>
                {isAdd ? (
                  <UsersForm
                    onSubmit={this.handleOnSubmit}
                    edit={editCircuito}
                    onClosed={this.handleAdd}
                  />
                ) : null}
              </Grid>
              <Grid item lg={4} sm={4} xl={3} xs={12}>
                {id !== null ? (
                  <UsersTable
                    data={steps}
                    onShowFormSteps={this.handleIsViewFormSteps}
                  />
                ) : null}
              </Grid>
              <Grid item lg={12} sm={12} xl={12} xs={12}>
                {isSteps ? (
                  <FormSteps
                    idCircuito={id}
                    onChangeSubmit={Add_Steps}
                    onAddPoint={Add_Points}
                    refreshData={this.handleRefresh}
                    points={points}
                    refreshDataSteps={this.handleRefreshSteps}
                    onShowFormSteps={this.handleIsViewFormSteps}
                    editSteps={editSteps}
                  />
                ) : null}
              </Grid>
              <Grid item lg={12} sm={12} xl={12} xs={12}>
                {id !== null ? (
                  <ContainsGrid
                    circuito_id={id}
                    data={cointainsTour}
                    onSubmit={this.handleAddConatinsTour}
                  />
                ) : null}
              </Grid>
            </Grid>
          </div>
        </div>
      </DashboardLayout>
    );
  }
}

Cotizador.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  Add_Circuito: PropTypes.func.isRequired,
  Add_Steps: PropTypes.func.isRequired,
  Add_Points: PropTypes.func.isRequired,
  points: PropTypes.array.isRequired,
  get_Points: PropTypes.func.isRequired,
  steps: PropTypes.array.isRequired,
  circuitos: PropTypes.array.isRequired,
  download_PDF: PropTypes.func.isRequired,
  get_ContainsTours: PropTypes.func.isRequired,
  cointainsTour: PropTypes.array.isRequired,
  post_AddContainsTour: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  return {
    idusers: state.user.idusers,
    points: state.user.points,
    steps: state.user.steps,
    circuitos: state.user.circuitos,
    cointainsTour: state.user.cointainsTour
  };
};

const mapDispatchToProps = dispatch => ({
  Add_Circuito: data => dispatch(AddCircuito(data)),
  Add_Steps: data => dispatch(AddSteps(data)),
  Add_Points: data => dispatch(AddPoints(data)),
  get_Points: id => dispatch(getPoints(id)),
  get_Steps: id => dispatch(getSteps(id)),
  get_Circuitos: () => dispatch(getCircuitos()),
  download_PDF: id => dispatch(downloadPDF(id)),
  get_ContainsTours: id => dispatch(getContainsTours(id)),
  post_AddContainsTour: data => dispatch(postAddContainsTour(data))
});

export default compose(
  withRouter,
  withStyles(styles),
  connect(mapStateToProps, mapDispatchToProps)
)(Cotizador);
