import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Button,
  TextField,
  CircularProgress,
  Typography,
  FormLabel
} from "@material-ui/core";

// Shared components
import {
  Portlet,
  PortletHeader,
  PortletLabel,
  PortletContent,
  PortletFooter
} from "../../../../components";

// Material components
import { IconButton } from "@material-ui/core";
import moment from "moment";
// Material icons
import InputAdornment from "@material-ui/core/InputAdornment";
import { ArrowBack as Back, Add, Remove } from "@material-ui/icons";

// Component styles
import styles from "./styles";

const type_tours_data = [
  { id: null, name: "" },
  { id: 1, name: "Mochilero" },
  { id: 2, name: "Selecto" },
  { id: 3, name: "Lujo" }
];

const init_tour_data = [
  { id: null, name: "" },
  { id: 1, name: "Normal" },
  { id: 2, name: "Reversa" }
];

const where_tour_data = [
  {
    groupName: "",
    child: [
      {
        id: null,
        name: ""
      }
    ]
  },
  {
    groupName: "Europa",
    child: [
      {
        id: 1,
        name: "Alemania"
      },
      { id: 2, name: "Amsterdam" }
    ]
  }
];
class UsersForm extends Component {
  state = {
    tours_cotizaciones: {
      id: null,
      start_date: null,
      end_date: null,
      number_personas: null,
      type_tour: null,
      init_tour: null,
      where_tour: null
    },
    isLoading: false,
    error: ""
  };

  componentDidMount = () => {
    const { infoEdit } = this.props;

    if (infoEdit) {
      /*
      const { firstName, lastName, email, password, userId } = infoEdit;
      this.setState({
        values: {
          firstName: firstName,
          lastName: lastName,
          email: email,
          password: password,
          userId: userId
        }
      });*/
    }
  };
  handleFieldChange = (field, value) => {
    const newState = { ...this.state };

    newState.tours_cotizaciones[field] = value;
    this.setState(newState);
  };

  handleSubmit = async () => {
    const { onSubmit } = this.props;
    const { tours_cotizaciones } = this.state;
    const {
      id,
      start_date,
      end_date,
      number_personas,
      type_tour,
      init_tour,
      where_tour
    } = tours_cotizaciones;

    this.setState({ isLoading: true });

    if (
      start_date !== null &&
      end_date !== null &&
      number_personas !== null &&
      type_tour !== null &&
      init_tour !== null &&
      where_tour !== null
    ) {
      let rs = await onSubmit(tours_cotizaciones);
      this.setState({
        isLoading: false,
        error: null
      });
    } else {
      this.setState({
        isLoading: false,
        error:
          "Ingrese los datos necesarios (Fecha inicio y fin, Numero de personas, Tipo de tour, Como empezará el tour, Donce comenzará el tour)"
      });
    }
  };

  handleSetValue = type => {
    const { tours_cotizaciones } = this.state;
    const { number_personas } = tours_cotizaciones;
    const newState = { ...this.state };
    if (type === "+") {
      newState.tours_cotizaciones.number_personas =
        (isNaN(number_personas) ? 0 : number_personas) + 1;
    } else if (type === "-") {
      let result = (isNaN(number_personas) ? 0 : number_personas) - 1;
      newState.tours_cotizaciones.number_personas = result;
      if (result <= 0) {
        newState.tours_cotizaciones.number_personas = 0;
      }
    }
    this.setState(newState);
  };

  render() {
    const { classes, className, handleClose, handleBack, ...rest } = this.props;
    const { tours_cotizaciones, isLoading, error } = this.state;
    const {
      start_date,
      end_date,
      number_personas,
      type_tour,
      init_tour,
      where_tour
    } = tours_cotizaciones;

    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet {...rest} className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"PROGRAMA TU TOUR"} />
        </PortletHeader>
        <PortletContent noPadding>
          <form autoComplete="off" noValidate>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                Elige las Fechas
              </FormLabel>
            </div>
            <div className={classes.fieldTwo}>
              <div className={classes.fielColumn}>
                <TextField
                  className={classes.inputField}
                  label=""
                  type="date"
                  margin="dense"
                  onChange={event =>
                    this.handleFieldChange("start_date", event.target.value)
                  }
                  required
                  value={start_date}
                  variant="outlined"
                />
              </div>
              <div className={classes.fielColumn}>
                <TextField
                  className={classes.inputField}
                  label=""
                  type="date"
                  margin="dense"
                  onChange={event =>
                    this.handleFieldChange("end_date", event.target.value)
                  }
                  required
                  value={end_date}
                  variant="outlined"
                  inputProps={{ min: moment(start_date).format("YYYY-MM-DD") }}
                />
              </div>
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                Introduce el numero de personas
              </FormLabel>
              <TextField
                className={classes.textField}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <IconButton
                        edge="start"
                        aria-label=""
                        className={classes.btnIcon}
                        onClick={() => {
                          this.handleSetValue("-");
                        }}
                      >
                        <Remove />
                      </IconButton>
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        edge="end"
                        aria-label=""
                        className={classes.btnIcon}
                        onClick={() => {
                          this.handleSetValue("+");
                        }}
                      >
                        <Add />
                      </IconButton>
                    </InputAdornment>
                  )
                }}
                type="number"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("number_personas", event.target.value)
                }
                required
                value={number_personas}
                variant="outlined"
              />
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                Elige el tipo de TOUR
              </FormLabel>
              <TextField
                className={classes.textField}
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("type_tour", event.target.value)
                }
                select
                SelectProps={{
                  native: true
                }}
                required
                value={type_tour}
                variant="outlined"
              >
                {type_tours_data.map(option => (
                  <option key={option.id} value={option.id}>
                    {option.name}
                  </option>
                ))}
              </TextField>
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                ¿Como se empezará el TOUR?
              </FormLabel>
              <TextField
                className={classes.textField}
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("init_tour", event.target.value)
                }
                select
                SelectProps={{
                  native: true
                }}
                required
                value={init_tour}
                variant="outlined"
              >
                {init_tour_data.map(option => (
                  <option key={option.id} value={option.id}>
                    {option.name}
                  </option>
                ))}
              </TextField>
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                ¿Donde Comenzará el TOUR?
              </FormLabel>
              <TextField
                className={classes.textField}
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("where_tour", event.target.value)
                }
                required
                select
                SelectProps={{
                  native: true
                }}
                value={where_tour}
                variant="outlined"
              >
                {where_tour_data.map(group => {
                  return (
                    <optgroup label={group.groupName}>
                      {group.child.map(item => {
                        return (
                          <option key={item.id} value={item.id}>
                            {item.name}
                          </option>
                        );
                      })}
                    </optgroup>
                  );
                })}
              </TextField>
            </div>
          </form>
        </PortletContent>
        <PortletFooter className={classes.portletFooter}>
          {error && (
            <Typography className={classes.submitError} variant="body2">
              {error}
            </Typography>
          )}

          {isLoading ? (
            <CircularProgress className={classes.progress} />
          ) : (
            <Button
              className={classes.btnSubmit}
              color="primary"
              variant="contained"
              onClick={this.handleSubmit}
            >
              SIGUIENTE
            </Button>
          )}
        </PortletFooter>
      </Portlet>
    );
  }
}

UsersForm.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func.isRequired
};

export default withStyles(styles)(UsersForm);
