import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";
import PerfectScrollbar from "react-perfect-scrollbar";

// Material helpers
import { withStyles, TextField, IconButton } from "@material-ui/core";

// Material components
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  TablePagination
} from "@material-ui/core";
import { EditOutlined as Edit, SaveOutlined as Save } from "@material-ui/icons";
import {
  Portlet,
  PortletContent,
  PortletHeader,
  PortletLabel
} from "../../../../components";

// Component styles
import styles from "./styles";

class ContainsGrid extends Component {
  state = {
    selectedUsers: [],
    rowsPerPage: 10,
    page: 0,
    id_edit: null,
    value_Edit: null,
    info: null
  };

  handleSelectAll = event => {
    const { users, onSelect } = this.props;

    let selectedUsers;

    if (event.target.checked) {
      selectedUsers = users.map(user => user.id);
    } else {
      selectedUsers = [];
    }

    this.setState({ selectedUsers });

    onSelect(selectedUsers);
  };

  handleSelectOne = (event, id) => {
    const { onSelect } = this.props;
    const { selectedUsers } = this.state;

    const selectedIndex = selectedUsers.indexOf(id);
    let newSelectedUsers = [];

    if (selectedIndex === -1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers, id);
    } else if (selectedIndex === 0) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(1));
    } else if (selectedIndex === selectedUsers.length - 1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelectedUsers = newSelectedUsers.concat(
        selectedUsers.slice(0, selectedIndex),
        selectedUsers.slice(selectedIndex + 1)
      );
    }

    this.setState({ selectedUsers: newSelectedUsers });

    onSelect(newSelectedUsers);
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  handleSave = async () => {
    const { value_Edit, info } = this.state;
    const { circuito_id, onSubmit } = this.props;
    let dto = {
      circuito_id,
      description: value_Edit,
      orderby: info ? info.orderby : null
    };

    let fun_response = await onSubmit(dto);
    this.setState({ value_Edit: '', info: null, id_edit: null });
  };

  render() {
    const { classes, className, data } = this.props;
    const { rowsPerPage, page, id_edit, value_Edit } = this.state;
    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"EL TOUR INCLUYE"} />
        </PortletHeader>
        <PortletContent noPadding>
          <PerfectScrollbar>
            <Table>
              <TableBody>
                {id_edit === null ? (
                  <TableRow className={classes.tableRow} hover key={0}>
                    <TableCell
                      className={classes.tableCell}
                      style={{
                        width: "90%",
                        whiteSpace: "normal",
                        wordWrap: "break-word"
                      }}
                    >
                      <TextField
                        value={value_Edit}
                        className={classes.textField}
                        onChange={event => {
                          this.setState({
                            value_Edit: event.target.value
                          });
                        }}
                        variant="outlined"
                      />
                    </TableCell>
                    <TableCell
                      className={classes.tableCell}
                      style={{ width: "10%" }}
                    >
                      <IconButton
                        onClick={() => {
                          this.handleSave();
                        }}
                      >
                        <Save />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ) : null}

                {data
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map(item => {
                    return (
                      <TableRow
                        className={classes.tableRow}
                        hover
                        key={item.orderby}
                      >
                        <TableCell
                          className={classes.tableCell}
                          style={{
                            width: "90%",
                            whiteSpace: "normal",
                            wordWrap: "break-word"
                          }}
                        >
                          {id_edit === item.orderby ? (
                            <TextField
                              value={value_Edit}
                              className={classes.textField}
                              onChange={event => {
                                this.setState({
                                  value_Edit: event.target.value
                                });
                              }}
                              variant="outlined"
                            />
                          ) : (
                            item.description
                          )}
                        </TableCell>
                        <TableCell
                          className={classes.tableCell}
                          style={{ width: "10%" }}
                        >
                          {id_edit === null ? (
                            <IconButton
                              onClick={() => {
                                this.setState({
                                  id_edit: item.orderby,
                                  value_Edit: item.description,
                                  info: item
                                });
                              }}
                            >
                              <Edit />
                            </IconButton>
                          ) : null}
                          {id_edit === item.orderby ? (
                            <IconButton
                              onClick={() => {
                                this.handleSave();
                              }}
                            >
                              <Save />
                            </IconButton>
                          ) : null}
                        </TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </PerfectScrollbar>
          <TablePagination
            backIconButtonProps={{
              "aria-label": "Previous Page"
            }}
            component="div"
            count={data.length}
            nextIconButtonProps={{
              "aria-label": "Next Page"
            }}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
            page={page}
            rowsPerPage={rowsPerPage}
            rowsPerPageOptions={[5, 10, 25]}
          />
        </PortletContent>
      </Portlet>
    );
  }
}

ContainsGrid.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  circuito_id: PropTypes.number.isRequired,
  onSelect: PropTypes.func,
  onShowDetails: PropTypes.func,
  searchText: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired,
  onShowFormSteps: PropTypes.func.isRequired,
  onAdd: PropTypes.func.isRequired,
  downloadEvent: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired
};

ContainsGrid.defaultProps = {
  data: [],
  onSelect: () => {},
  onShowDetails: () => {},
  onShowFormSteps: () => {},
  onAdd: () => {}
};

export default withStyles(styles)(ContainsGrid);
