import apii from "../services/apii";
export const LOGIN_USER = "LOGIN_USER";
export const GET_EMPLOYE = "GET_EMPLOYE";
export const GET_INCIDENCIAS = "GET_INCIDENCIAS";
export const GET_ERROR = "GET_ERROR";
export const GET_COMMENTS = "GET_COMMENTS";
export const GET_POINTS = "GET_POINTS";
export const GET_STEPS = "GET_STEPS";
export const GET_CIRCUITOS = "GET_CIRCUITOS";
export const GET_PERSON_TOUR = "GET_PERSON_TOUR";
export const GET_CONTAINS_TOUR = "GET_CONTAINS_TOUR";

export const setUserData = user => ({
  type: LOGIN_USER,
  user
});

export const setEmploye = employe => ({
  type: GET_EMPLOYE,
  employe
});

export const setIncidencias = incidencias => ({
  type: GET_INCIDENCIAS,
  incidencias
});

export const setError = error => ({
  type: GET_ERROR,
  error
});

export const setComments = comments => ({
  type: GET_COMMENTS,
  comments
});

export const setPoints = points => ({
  type: GET_POINTS,
  points
});

export const setGetPoints = steps => ({
  type: GET_STEPS,
  steps
});

export const setCircuitos = circuitos => ({
  type: GET_CIRCUITOS,
  circuitos
});

export const setPersonTour = person => ({
  type: GET_PERSON_TOUR,
  person
});

export const setContainsTours = cointainsTour => ({
  type: GET_CONTAINS_TOUR,
  cointainsTour
});

export const LoginUserApp = user_data => async dispatch => {
  const response = await apii.postLogin(user_data);
  if (response.ok) {
    const payload = response.data;
    if (payload) {
      dispatch(setUserData(payload));
      return true;
    } else {
      return false;
    }
  } else {
    return false;
  }
};

export const getEmploye = () => async dispatch => {
  const response = await apii.getEmploye();
  if (response.ok) {
    const payload = response.data;
    if (payload) {
      dispatch(setEmploye(payload));
      return true;
    } else {
      return false;
    }
  } else {
    const payload = response.data;
    dispatch(setError(payload.message));
    return false;
  }
};

