import React, { Component } from "react";
import PropTypes from "prop-types";
import { Grid } from "@material-ui/core";

import { Dashboard as DashboardLayout } from "../../layouts";
import { withStyles, Typography } from "@material-ui/core";
import { UsersForm, UsersTable, FormPerson, Budget, Users } from "./components";

//redux
import { connect } from "react-redux";
import {
  setTour,
  getCircuitos,
  postadd_person_tour,
  get_person_tour,
  downloadCONTRATO
} from "../../actions/user";

const styles = theme => ({
  root: {},
  item: {
    height: "100%"
  },
  bodyHeader: {
    background:
      "radial-gradient(at 50% -20%, #1967a9, #0a1832) fixed !important",
    paddingBottom: 85
  },
  textTitle: {
    color: "#ffffff",
    paddingTop: 15,
    paddingBottom: 15,
    marginLeft: 10
    //marginTop: 15
  },
  gridFloat: {
    position: "relative",
    top: -50
  }
});

class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = { isNext: false, id_tour: null };
  }

  componentDidMount = async () => {
    const { get_Circuitos, get_person_tour_fun } = this.props;
    await get_Circuitos();
    await get_person_tour_fun();
  };

  handleSubmitApi = async data => {
    const { set_Tour, get_Circuitos } = this.props;
    let response = await set_Tour(data);
    if (response) {
      await get_Circuitos();
      this.setState({ isNext: true, id_tour: response.id });
    }
    return response;
  };

  handleSubmitPerson = async data => {
    const { add_person_tour, get_person_tour_fun } = this.props;
    let response = await add_person_tour(data);
    await get_person_tour_fun();

    return response;
  };
  render() {
    const { classes, circuitos, person, download_CONTRATO } = this.props;
    const { isNext, id_tour } = this.state;
    return (
      <DashboardLayout title="Administración">
        <div className={classes.root}>
          <div className={classes.bodyHeader}>
            <Typography variant={"h2"} className={classes.textTitle}>
              Dashboard SITAV
            </Typography>
            <Grid container spacing={2}>
              <Grid item lg={4} sm={6} xl={12} xs={12}>
                {!isNext ? (
                  <UsersForm onSubmit={this.handleSubmitApi} />
                ) : (
                  <FormPerson
                    selectDataTour={circuitos}
                    onSubmit={this.handleSubmitPerson}
                    id_tour={id_tour}
                  />
                )}
              </Grid>
              <Grid item lg={8} sm={6} xl={12} xs={12}>
                <UsersTable data={person} onDowload={download_CONTRATO} />
              </Grid>
            </Grid>
          </div>
          <Grid container spacing={1} className={classes.gridFloat}>
            <Grid item lg={3} sm={3} xl={6} xs={12}>
              <Budget
                textNumber={200}
                textTiltle={"Ventas Cerradas"}
                color={"#4bbbce"}
              />
            </Grid>
            <Grid item lg={3} sm={3} xl={6} xs={12}>
              <Budget
                textNumber={200}
                textTiltle={"Nuevas Cotizaciones"}
                color={"#8862e0"}
              />
            </Grid>
            <Grid item lg={3} sm={3} xl={6} xs={12}>
              <Budget
                textNumber={200}
                textTiltle={"Nuevos Usuarios"}
                color={"#2f8ee0"}
              />
            </Grid>
            <Grid item lg={3} sm={3} xl={6} xs={12}>
              <Budget
                textNumber={200}
                textTiltle={"Pendientes"}
                color={"#f24734"}
              />
            </Grid>
          </Grid>
        </div>
      </DashboardLayout>
    );
  }
}

Admin.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  circuitos: PropTypes.array.isRequired,
  set_Tour: PropTypes.func.isRequired,
  get_Circuitos: PropTypes.func.isRequired,
  person: PropTypes.array.isRequired,
  get_person_tour_fun: PropTypes.array.isRequired,
  download_CONTRATO: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  return {
    idusers: state.user.idusers,
    circuitos: state.user.circuitos,
    person: state.user.person
  };
};

const mapDispatchToProps = dispatch => ({
  set_Tour: data => dispatch(setTour(data)),
  get_Circuitos: () => dispatch(getCircuitos()),
  add_person_tour: data => dispatch(postadd_person_tour(data)),
  get_person_tour_fun: () => dispatch(get_person_tour()),
  download_CONTRATO: id => dispatch(downloadCONTRATO(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Admin));
