import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Button,
  TextField,
  CircularProgress,
  Typography,
  Grid,
  IconButton
} from "@material-ui/core";

import { CloseOutlined } from "@material-ui/icons";
// Shared components
import {
  Portlet,
  PortletHeader,
  PortletLabel,
  PortletContent,
  PortletFooter
} from "../../../../components";

// Component styles
import styles from "./styles";
import { GridTable } from "./components";

class FormSteps extends Component {
  state = {
    Steps: {
      id: null,
      countryName: "",
      circuito_id: null
    },
    isLoading: false,
    error: ""
  };

  componentDidMount = () => {
    const { editSteps, idCircuito, refreshData } = this.props;
    if (editSteps) {
      const newState = { ...this.state };

      newState.Steps["id"] = editSteps.id;
      newState.Steps["countryName"] = editSteps.countryName;
      newState.Steps["circuito_id"] = idCircuito;
      //refreshDataSteps(editSteps.id);
      refreshData(editSteps.id);
      this.setState(newState);
    }
  };

  handleFieldChange = (field, value) => {
    const newState = { ...this.state };
    newState.Steps[field] = value;
    this.setState(newState);
  };

  handleSubmit = async () => {
    const { Steps } = this.state;
    const { idCircuito, onChangeSubmit, refreshDataSteps } = this.props;

    const { countryName, id } = Steps;
    this.setState({ isLoading: true });
    let dto = {
      countryName,
      circuito_id: idCircuito,
      id: id
    };

    if (countryName !== "") {
      let response = await onChangeSubmit(dto);
      if (response) {
        const newState = { ...this.state };
        newState.Steps["id"] = response.id;
        newState.Steps["circuito_id"] = idCircuito;
        newState.isLoading = false;
        this.setState(newState);
        refreshDataSteps(idCircuito);
      } else {
        this.setState({
          isLoading: false,
          error: ""
        });
      }
    } else {
      this.setState({
        isLoading: false,
        error: "Ingrese el nombre"
      });
    }
  };

  handleAdd = async data => {
    const { refreshData, onAddPoint } = this.props;
    const { Steps } = this.state;
    const { id } = Steps;
    let responseOne = await onAddPoint({
      steps_id: data.steps_id,
      date_event: data.date_event,
      description: data.description,
      price: data.price,
      numberDay: data.numberDay,
      date_steps_id: data.date_steps_id
    });
    let responseTwo = await refreshData(id);
    return true;
  };
  render() {
    const { classes, className, points, onShowFormSteps, ...rest } = this.props;
    const { Steps, isLoading } = this.state;
    const { id, countryName } = Steps;
    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet {...rest} className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"CIUDA O PAÍS"} />
          <div className={classes.row}>
            <IconButton
              onClick={() => {
                const newState = { ...this.state };
                newState.Steps["countryName"] = "";
                newState.Steps["id"] = null;
                this.setState(newState);
                onShowFormSteps(false);
              }}
            >
              <CloseOutlined />
            </IconButton>
          </div>
        </PortletHeader>
        <PortletContent noPadding>
          <form autoComplete="off" noValidate>
            <div className={classes.field}>
              <TextField
                className={classes.textField}
                helperText="Ingresa el nombre"
                label="Ciudad"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("countryName", event.target.value)
                }
                required
                value={countryName}
                variant="outlined"
              />
              <Button
                className={classes.btnSubmit}
                color="primary"
                variant="contained"
                onClick={() => {
                  if (!isLoading) {
                    this.handleSubmit();
                  }
                }}
              >
                {isLoading ? (
                  <CircularProgress
                    className={classes.progress}
                  ></CircularProgress>
                ) : (
                  "GUARDAR"
                )}
              </Button>
            </div>
            {id !== null ? (
              <Grid container spacing={1}>
                <Grid item lg={12} md={12} xl={12} xs={12}>
                  <GridTable
                    onClickBtn={this.handleAdd}
                    idSteps={id}
                    data={points}
                  />
                </Grid>
              </Grid>
            ) : null}
          </form>
        </PortletContent>
      </Portlet>
    );
  }
}

FormSteps.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  onChangeSubmit: PropTypes.func.isRequired,
  idCircuito: PropTypes.number.isRequired,
  refreshData: PropTypes.func.isRequired,
  refreshDataSteps: PropTypes.func.isRequired,
  points: PropTypes.array.isRequired,
  onAddPoint: PropTypes.func.isRequired,
  onShowFormSteps: PropTypes.func.isRequired,
  editSteps: PropTypes.object.isRequired
};

export default withStyles(styles)(FormSteps);
