import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Button,
  TextField,
  CircularProgress,
  Typography
} from "@material-ui/core";

// Shared components
import {
  Portlet,
  PortletHeader,
  PortletLabel,
  PortletContent,
  PortletFooter
} from "../../../../components";

// Component styles
import styles from "./styles";
import moment from "moment";

class UsersForm extends Component {
  state = {
    circuito: {
      id: null,
      name: "",
      create_date: "",
      cost: null
    },
    isLoading: false,
    error: ""
  };

  componentDidMount = () => {
    const { edit } = this.props;
    if (edit) {
      const newState = { ...this.state };
      newState.circuito["id"] = edit.id;
      newState.circuito["name"] = edit.name;
      newState.circuito["cost"] = edit.cost;
      newState.circuito["create_date"] = moment(edit.create_date).format(
        "YYYY-MM-DD"
      );

      this.setState(newState);
    }
  };
  handleFieldChange = (field, value) => {
    const newState = { ...this.state };
    newState.circuito[field] = value;
    this.setState(newState);
  };

  handleSubmit = async () => {
    const { circuito } = this.state;
    const { onSubmit } = this.props;
    const { id, name, create_date, cost } = circuito;
    this.setState({ isLoading: true });
    let dto = {
      name,
      create_date,
      id,
      cost
    };

    if (name !== "" && create_date !== "") {
      let response = await onSubmit(dto);
      if (response) {
        this.setState({
          isLoading: false,
          error: ""
        });
      }
    } else {
      this.setState({
        isLoading: false,
        error: "Ingrese los datos necesarios (Nombre y la fecha)"
      });
    }
  };

  render() {
    const { classes, className, onClosed, ...rest } = this.props;
    const { circuito, isLoading, error } = this.state;
    const { name, create_date, cost, id } = circuito;
    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet {...rest} className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"CREA EL CIRCUITO"} />
        </PortletHeader>
        <PortletContent noPadding>
          <form autoComplete="off" noValidate>
            <div className={classes.field}>
              <TextField
                className={classes.textField}
                helperText="Ingresa el nombre"
                label="Nombre"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("name", event.target.value)
                }
                required
                value={name}
                variant="outlined"
              />
              <TextField
                className={classes.textField}
                helperText="Costo"
                label="Costo"
                margin="dense"
                type="number"
                onChange={event =>
                  this.handleFieldChange("cost", event.target.value)
                }
                required
                value={cost}
                variant="outlined"
              />
              <TextField
                className={classes.textField}
                label="FECHA"
                type="date"
                margin="dense"
                InputLabelProps={{
                  shrink: true
                }}
                onChange={event =>
                  this.handleFieldChange("create_date", event.target.value)
                }
                required
                value={create_date}
                variant="outlined"
              />
            </div>
          </form>
        </PortletContent>
        <PortletFooter className={classes.portletFooter}>
          {error && (
            <Typography className={classes.submitError} variant="body2">
              {error}
            </Typography>
          )}

          {isLoading ? (
            <CircularProgress className={classes.progress} />
          ) : (
            <Button
              className={classes.btnSubmit}
              color="primary"
              variant="contained"
              onClick={this.handleSubmit}
            >
              {id ? "ACTUALIZAR" : "GUARDAR"}
            </Button>
          )}
          {!isLoading && (
            <Button
              className={classes.btnSubmit}
              color="primary"
              variant="contained"
              onClick={() => {
                onClosed(false);
              }}
            >
              CANCELAR
            </Button>
          )}
        </PortletFooter>
      </Portlet>
    );
  }
}

UsersForm.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func.isRequired,
  edit: PropTypes.func.isRequired,
  onClosed: PropTypes.func.isRequired
};

export default withStyles(styles)(UsersForm);
