import apisauce from "apisauce";

const isDebug = false;
/**
 * Manages the api calls and returns the available endpoints
 * @param {string} baseURL Url to be used for api calls
 */
const create = (
  baseURL = isDebug
    ? "http://localhost/tours_api/api/"
    : "http://mits.centralus.cloudapp.azure.com/vuela_api/api/"
) => {
  const api = apisauce.create({
    baseURL,
    timeout: 35000
  });

  // Login
  const postLogin = data => api.post("User/loginUser", data);
  const getEmploye = () => api.get("Employe/get_employes");
  const getIncidencias = sendId => api.get(`Ticket/getTickets/${sendId}`);
  const postAddComment = data => api.post("Ticket/addComments", data);
  const getComment = idincidence => api.get(`Ticket/getComment/${idincidence}`);
  const postAddIncidencia = data => api.post("Ticket/addIncidecnias", data);
  const postAddCircuito = data => api.post("Circuito/addCircuito", data);
  const postAddSteps = data => api.post("Circuito/addSteps", data);
  const postAddPoints = data => api.post("Circuito/addPoints", data);
  const getPoints = id => api.get(`Circuito/get_Point/${id}`);
  const getSteps = id => api.get(`Circuito/get_Steps/${id}`);
  const getCircuitos = () => api.get("Circuito/get_circuito");
  const downloadPDF = id => api.get(`PdfCreator/generaPDF/${id}`);
  const setTour = data => api.post("Circuito/add_tours_cotizaciones", data);
  const postadd_person_tour = data =>
    api.post("Circuito/add_person_tour", data);
  const getPersonTours = () => api.get("circuito/get_person_tour");
  const generaContrato = id => api.get(`PdfCreator/generaContrato/${id}`);
  const getContainsTours = id => api.get(`Circuito/get_contains_tors/${id}`);
  const postAddContainsTour = data =>
    api.post("circuito/add_contains_tour", data);

  const setHeader = () => {
    api.setHeader("responseType", "blob");
  };

  return {
    postLogin,
    getEmploye,
    getIncidencias,
    postAddComment,
    getComment,
    postAddIncidencia,
    postAddCircuito,
    postAddSteps,
    postAddPoints,
    getPoints,
    getSteps,
    getCircuitos,
    downloadPDF,
    setHeader,
    setTour,
    postadd_person_tour,
    getPersonTours,
    generaContrato,
    getContainsTours,
    postAddContainsTour
  };
};

const apii = create();

export default apii;
