import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Button,
  TextField,
  CircularProgress,
  Typography,
  FormLabel
} from "@material-ui/core";

// Shared components
import {
  Portlet,
  PortletHeader,
  PortletLabel,
  PortletContent,
  PortletFooter
} from "../../../../components";

// Component styles
import styles from "./styles";

const type_tours_data = [
  { id: null, name: "" },
  { id: 1, name: "Mochilero" },
  { id: 2, name: "Selecto" },
  { id: 3, name: "Lujo" }
];

class FormPerson extends Component {
  state = {
    persona_send_tours: {
      id_person: null,
      name_person: null,
      email: null,
      folio_passport: null,
      id_circuito: null,
      id_tour_cotizacion: 1
    },
    isLoading: false,
    error: ""
  };

  componentDidMount = () => {
    const { infoEdit, id_tour } = this.props;
    if (id_tour) {
      this.handleFieldChange("id_tour_cotizacion", id_tour);
    }

    if (infoEdit) {
    }
  };
  handleFieldChange = (field, value) => {
    const newState = { ...this.state };
    newState.persona_send_tours[field] = value;
    this.setState(newState);
  };

  handleSubmit = async () => {
    const { onSubmit } = this.props;
    const { persona_send_tours } = this.state;
    const {
      name_person,
      email,
      folio_passport,
      id_circuito,
      id_tour_cotizacion
    } = persona_send_tours;

    this.setState({ isLoading: true });

    if (
      name_person !== null &&
      email !== null &&
      folio_passport !== null &&
      id_circuito !== null
    ) {
      let rs = await onSubmit(persona_send_tours);
      this.setState({
        id_person: null,
        name_person: null,
        email: null,
        folio_passport: null,
        id_circuito: null,
        id_tour_cotizacion: id_tour_cotizacion,
        isLoading: false,
        error: null
      });
    } else {
      this.setState({
        isLoading: false,
        error:
          "Ingrese los datos necesarios (Nombre, Correo, Numero Pasaporte, Tour)"
      });
    }
  };

  render() {
    const { classes, className, selectDataTour, ...rest } = this.props;
    const { persona_send_tours, isLoading, error } = this.state;
    const {
      name_person,
      email,
      folio_passport,
      id_circuito
    } = persona_send_tours;

    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet {...rest} className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"REGISTRA A LA PERSONA"} />
        </PortletHeader>
        <PortletContent noPadding>
          <form autoComplete="off" noValidate>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                Nombre de la persona
              </FormLabel>
              <TextField
                className={classes.textField}
                type="text"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("name_person", event.target.value)
                }
                required
                value={name_person}
                variant="outlined"
              />
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>Correo</FormLabel>
              <TextField
                className={classes.textField}
                type="email"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("email", event.target.value)
                }
                required
                value={email}
                variant="outlined"
              />
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>
                NUMERO PASAPORTE
              </FormLabel>
              <TextField
                className={classes.textField}
                type="text"
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("folio_passport", event.target.value)
                }
                required
                value={folio_passport}
                variant="outlined"
              />
            </div>
            <div className={classes.field}>
              <FormLabel className={classes.labelForm}>ELIGE EL TOUR</FormLabel>
              <TextField
                className={classes.textField}
                margin="dense"
                onChange={event =>
                  this.handleFieldChange("id_circuito", event.target.value)
                }
                select
                SelectProps={{
                  native: true
                }}
                required
                value={id_circuito}
                variant="outlined"
              >
                {selectDataTour.map(option => (
                  <option key={option.id} value={option.id}>
                    {option.name}
                  </option>
                ))}
              </TextField>
            </div>
          </form>
        </PortletContent>
        <PortletFooter className={classes.portletFooter}>
          {error && (
            <Typography className={classes.submitError} variant="body2">
              {error}
            </Typography>
          )}

          {isLoading ? (
            <CircularProgress className={classes.progress} />
          ) : (
            <Button
              className={classes.btnSubmit}
              color="primary"
              variant="contained"
              onClick={this.handleSubmit}
            >
              REGISTAR
            </Button>
          )}
        </PortletFooter>
      </Portlet>
    );
  }
}

FormPerson.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  id_tour: PropTypes.number,
  onSubmit: PropTypes.func.isRequired,
  selectDataTour: PropTypes.func.isRequired
};

export default withStyles(styles)(FormPerson);
