import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";
import moment from "moment";
import PerfectScrollbar from "react-perfect-scrollbar";

import { green } from "@material-ui/core/colors";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination,
  Button,
  IconButton
} from "@material-ui/core";

import {
  FiberManualRecord as Cicle,
  PlusOneOutlined,
  AddCircle,
  CreateOutlined
} from "@material-ui/icons";
import {
  Portlet,
  PortletContent,
  PortletHeader,
  PortletLabel
} from "../../../../components";

// Component styles
import styles from "./styles";

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    "&$checked": {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color="default" {...props} />);

class TableGrid extends Component {
  state = {
    selectedUsers: [],
    rowsPerPage: 10,
    page: 0
  };

  handleSelectAll = event => {
    const { users, onSelect } = this.props;

    let selectedUsers;

    if (event.target.checked) {
      selectedUsers = users.map(user => user.id);
    } else {
      selectedUsers = [];
    }

    this.setState({ selectedUsers });

    onSelect(selectedUsers);
  };

  handleSelectOne = (event, id) => {
    const { onSelect } = this.props;
    const { selectedUsers } = this.state;

    const selectedIndex = selectedUsers.indexOf(id);
    let newSelectedUsers = [];

    if (selectedIndex === -1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers, id);
    } else if (selectedIndex === 0) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(1));
    } else if (selectedIndex === selectedUsers.length - 1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelectedUsers = newSelectedUsers.concat(
        selectedUsers.slice(0, selectedIndex),
        selectedUsers.slice(selectedIndex + 1)
      );
    }

    this.setState({ selectedUsers: newSelectedUsers });

    onSelect(newSelectedUsers);
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  render() {
    const {
      classes,
      className,
      searchText,
      data,
      onAdd,
      downloadEvent
    } = this.props;
    const { selectedUsers, rowsPerPage, page } = this.state;
    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"COTIZACIONES"} />
          <div className={classes.row}>
            <IconButton
              onClick={() => {
                onAdd(true);
              }}
            >
              <AddCircle />
            </IconButton>
          </div>
        </PortletHeader>
        <PortletContent noPadding>
          <PerfectScrollbar>
            <Table>
              <TableBody>
                {data
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map(item => {
                    return (
                      <TableRow
                        className={classes.tableRow}
                        hover
                        key={item.id}
                      >
                        <TableCell className={classes.tableCell}>
                          {item.name}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          {moment(item.create_date).format("YYYY-MM-DD")}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          $ {item.cost.toFixed(2)}
                        </TableCell>
                        <TableCell>
                          <IconButton
                            onClick={() => {
                              onAdd(true, item);
                            }}
                          >
                            <CreateOutlined />
                          </IconButton>
                        </TableCell>
                        <TableCell>
                          <Button
                            className={classes.dowloadBtn}
                            color="primary"
                            variant="contained"
                            onClick={async () => {
                              let response = await downloadEvent(item.id);
                              if (response) {
                                const link = document.createElement("a");
                                link.href =
                                  "http://mits.centralus.cloudapp.azure.com/vuela_api/Uploads/PDF/" +
                                  response;
                                link.setAttribute("target", "_blank");
                                link.setAttribute("download", "reporte.pdf");
                                document.body.appendChild(link);
                                link.click();
                              }

                              console.log(response);
                            }}
                          >
                            DESCARGAR PDF
                          </Button>
                        </TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </PerfectScrollbar>
          <TablePagination
            backIconButtonProps={{
              "aria-label": "Previous Page"
            }}
            component="div"
            count={data.length}
            nextIconButtonProps={{
              "aria-label": "Next Page"
            }}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
            page={page}
            rowsPerPage={rowsPerPage}
            rowsPerPageOptions={[5, 10, 25]}
          />
        </PortletContent>
      </Portlet>
    );
  }
}

TableGrid.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  onSelect: PropTypes.func,
  onShowDetails: PropTypes.func,
  searchText: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired,
  onShowFormSteps: PropTypes.func.isRequired,
  onAdd: PropTypes.func.isRequired,
  downloadEvent: PropTypes.func.isRequired
};

TableGrid.defaultProps = {
  data: [],
  onSelect: () => {},
  onShowDetails: () => {},
  onShowFormSteps: () => {},
  onAdd: () => {}
};

export default withStyles(styles)(TableGrid);
