import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";
import moment from "moment";
import PerfectScrollbar from "react-perfect-scrollbar";

import { green } from "@material-ui/core/colors";

// Material helpers
import { withStyles } from "@material-ui/core";

// Material components
import {
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination,
  Button
} from "@material-ui/core";

import { FiberManualRecord as Cicle } from "@material-ui/icons";
import {
  Portlet,
  PortletContent,
  PortletHeader,
  PortletLabel
} from "../../../../components";

// Component styles
import styles from "./styles";

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    "&$checked": {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color="default" {...props} />);

class UsersTable extends Component {
  state = {
    selectedUsers: [],
    rowsPerPage: 10,
    page: 0
  };

  handleSelectAll = event => {
    const { users, onSelect } = this.props;

    let selectedUsers;

    if (event.target.checked) {
      selectedUsers = users.map(user => user.id);
    } else {
      selectedUsers = [];
    }

    this.setState({ selectedUsers });

    onSelect(selectedUsers);
  };

  handleSelectOne = (event, id) => {
    const { onSelect } = this.props;
    const { selectedUsers } = this.state;

    const selectedIndex = selectedUsers.indexOf(id);
    let newSelectedUsers = [];

    if (selectedIndex === -1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers, id);
    } else if (selectedIndex === 0) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(1));
    } else if (selectedIndex === selectedUsers.length - 1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelectedUsers = newSelectedUsers.concat(
        selectedUsers.slice(0, selectedIndex),
        selectedUsers.slice(selectedIndex + 1)
      );
    }

    this.setState({ selectedUsers: newSelectedUsers });

    onSelect(newSelectedUsers);
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  render() {
    const { classes, className, onDowload, data } = this.props;
    const { selectedUsers, rowsPerPage, page } = this.state;
    const rootClassName = classNames(classes.root, className);
    const colorsCicle = ["#6fd088", "#ffbb44 ", "#f24734 "];
    return (
      <Portlet className={rootClassName}>
        <PortletHeader>
          <PortletLabel title={"ÚLTIMAS COTIZACIONES REALIZADAS"} />
        </PortletHeader>
        <PortletContent noPadding>
          <PerfectScrollbar>
            <Table>
              <TableBody>
                {data
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map(item => {
                    let myColor =
                      item.status === "Confirm"
                        ? 0
                        : item.status === "Waiting payment"
                        ? 1
                        : 2;
                    return (
                      <TableRow
                        className={classes.tableRow}
                        hover
                        key={item.id_person}
                      >
                        <TableCell className={classes.tableCell}>
                          {item.name_person}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          <Cicle
                            style={{
                              color: colorsCicle[myColor],
                              display: "inline-flex",
                              marginBottom: "-5px"
                            }}
                          />
                          {item.status}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          ${item.costo.toFixed(2)}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          {moment(item.start_date).format("DD/MM/YYYY")}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          <Button
                            className={classes.dowloadBtn}
                            color="primary"
                            variant="contained"
                            onClick={async () => {
                              let response = await onDowload(item.id_person);
                              if (response) {
                                const link = document.createElement("a");
                                link.href =
                                  "http://mits.centralus.cloudapp.azure.com/vuela_api/Uploads/PDF/" +
                                  response;
                                link.setAttribute("target", "_blank");
                                link.setAttribute("download", "reporte.pdf");
                                document.body.appendChild(link);
                                link.click();
                              }
                            }}
                          >
                            DESCARGAR PDF
                          </Button>
                        </TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </PerfectScrollbar>
          <TablePagination
            backIconButtonProps={{
              "aria-label": "Previous Page"
            }}
            component="div"
            count={data.length}
            nextIconButtonProps={{
              "aria-label": "Next Page"
            }}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
            page={page}
            rowsPerPage={rowsPerPage}
            rowsPerPageOptions={[5, 10, 25]}
          />
        </PortletContent>
      </Portlet>
    );
  }
}

UsersTable.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  onSelect: PropTypes.func,
  onShowDetails: PropTypes.func,
  onDowload: PropTypes.func,
  searchText: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired
};

UsersTable.defaultProps = {
  data: [],
  onSelect: () => {},
  onShowDetails: () => {}
};

export default withStyles(styles)(UsersTable);
