import React, { Component } from "react";

// Externals
import classNames from "classnames";
import PropTypes from "prop-types";
import PerfectScrollbar from "react-perfect-scrollbar";

// Material helpers
import { withStyles, IconButton, TextField } from "@material-ui/core";

// Material components
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination
} from "@material-ui/core";
import {
  CreateOutlined as EditIcon,
  DeleteOutlined as DeeleteIcon,
  DoneOutlined as ActiveIcon,
  AddOutlined,
  EditOutlined as Edit,
  SaveOutlined as Save
} from "@material-ui/icons";
// Shared components
import { Portlet, PortletContent } from "../../../../../../components";
import moment from "moment";
// Component styles
import styles from "./styles";

class GridTable extends Component {
  state = {
    selectedUsers: [],
    rowsPerPage: 5,
    page: 0,
    checkProductId: null,
    points: {
      date_steps_id: null,
      steps_id: null,
      date_event: "",
      description: "",
      price: "",
      numberDay: 0
    }
  };

  componentDidMount = () => {
    const { idSteps } = this.props;
    this.handleFieldChange("steps_id", idSteps);
  };

  handleSelectOne = (event, item) => {
    const { onSelect } = this.props;
    onSelect(item);
    this.setState({ checkProductId: item.productId });
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  handleFieldChange = (field, value) => {
    const newState = { ...this.state };
    newState.points[field] = value;
    this.setState(newState);
  };

  render() {
    const { classes, className, data, onClickBtn } = this.props;
    const { selectedUsers, rowsPerPage, page, points } = this.state;
    const rootClassName = classNames(classes.root, className);
    return (
      <Portlet className={rootClassName}>
        <PortletContent noPadding>
          <PerfectScrollbar>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell align="center">DÍA</TableCell>
                  <TableCell align="center">DESCRIPCIÓN</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {points.date_steps_id ? null : (
                  <TableRow className={classes.tableRow} hover key={0}>
                    <TableCell className={classes.tableCell} align="center">
                      {" "}
                    </TableCell>
                    <TableCell className={classes.tableCell} align="center">
                      <TextField
                        className={classes.textField}
                        style={{ width: "100%" }}
                        margin="dense"
                        onChange={event =>
                          this.handleFieldChange(
                            "description",
                            event.target.value
                          )
                        }
                        required
                        value={points.description}
                        variant="outlined"
                      />
                    </TableCell>
                    <TableCell align="left">
                      <IconButton
                        onClick={async () => {
                          let band = await onClickBtn(points);
                          if (band) {
                            const newState = { ...this.state };
                            newState.points["description"] = "";
                            newState.points["date_event"] = "";
                            newState.points["price"] = "";
                            this.setState(newState);
                          }
                        }}
                      >
                        <Save />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                )}

                {data
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((item, index) => (
                    <TableRow
                      className={classes.tableRow}
                      hover
                      key={item.date_steps_id}
                      selected={
                        selectedUsers.indexOf(item.date_steps_id) !== -1
                      }
                    >
                      <TableCell className={classes.tableCell}>
                        DÍA {item.numberDay}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {points.date_steps_id === item.date_steps_id ? (
                          <TextField
                            className={classes.textField}
                            style={{ width: "100%" }}
                            margin="dense"
                            onChange={event =>
                              this.handleFieldChange(
                                "description",
                                event.target.value
                              )
                            }
                            required
                            value={points.description}
                            variant="outlined"
                          />
                        ) : (
                          item.description
                        )}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {points.date_steps_id === item.date_steps_id ? (
                          <IconButton
                            onClick={async () => {
                              console.log(points)
                              
                              let band = await onClickBtn(points);
                              if (band) {
                                const newState = { ...this.state };
                                newState.points["description"] = null;
                                newState.points["date_event"] = null;
                                newState.points["price"] = null;
                                newState.points["date_steps_id"] = null;

                                this.setState(newState);
                              }
                            }}
                          >
                            <Save />
                          </IconButton>
                        ) : (
                          <IconButton
                            onClick={() => {
                              this.setState({
                                points: {
                                  date_steps_id: item.date_steps_id,
                                  steps_id: item.steps_id,
                                  date_event: item.date_event,
                                  description: item.description,
                                  price: item.price,
                                  numberDay: item.numberDay
                                }
                              });
                            }}
                          >
                            <Edit />
                          </IconButton>
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </PerfectScrollbar>
          <TablePagination
            backIconButtonProps={{
              "aria-label": "Previous Page"
            }}
            component="div"
            count={data.length}
            nextIconButtonProps={{
              "aria-label": "Next Page"
            }}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
            page={page}
            rowsPerPage={rowsPerPage}
            rowsPerPageOptions={[5, 10, 15]}
          />
        </PortletContent>
      </Portlet>
    );
  }
}

GridTable.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  data: PropTypes.array.isRequired,
  idSteps: PropTypes.number.isRequired,
  onClickBtn: PropTypes.func.isRequired
};

GridTable.defaultProps = {
  data: [],
  onSelect: () => {},
  onClickBtn: () => {}
};

export default withStyles(styles)(GridTable);
